<template>
<div>
  <form>
    <input type = "file" @change="getFile($event)">
    <button type = "button" @click="fileUpload($event)">Upload</button>

  </form>
</div>
</template>
<script>
import { request } from '../assets/js/utils/request.js'
export default {
  data () {
    return {
      file: ''
    }
  },
  name: 'Upload',
  methods: {
    getFile (event) {
      this.file = event.target.files[0]
      console.log(this.file)
    },
    fileUpload (event) {
      const formData = new FormData()
      formData.append('file', this.file)
      // eslint-disable-next-line no-undef
      request(process.env.VUE_APP_DOMAIN + '/file/upload', 'post', formData).then(res => {
        console.log(res)
      })
    }
  }
}

</script>
